.travel {
    height: 100%;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    position: relative;
    @include tablet {
        background-size: 158% 100%;
        background-position: 50%;
    }
    @include mobileL {
        background-size: 190% 100%;
    }
    &__title {
        font-family: $fontFamily-medivalSharp;
        color: $color-blue;
        text-shadow: 0.063rem 0.063rem $color-gray_dark;
        font-weight: bold;
        word-spacing: 0.188rem;
        text-transform: uppercase;
        text-align: center;

        font-size: 2.75rem;
        letter-spacing: 0.125rem;
        margin-bottom: 35px;
        margin-top: 10px;
        padding: 10px;
        background: rgba(255, 255, 255, 0.5);
        border-radius: 20px;
        backdrop-filter: blur(4px);
        @include laptopS {
            border-radius: 10px;
            padding: 5px;
        }
        @include mobileL {
            font-size: 22px;
        }
        @include mobileM {
            font-size: 20px;
            border-radius: 7px;
            margin-top: 6px;
        }
    }
    &__countdownWrapper {
        width: 100%;
        @include tablet {
            margin-bottom: 30px;
        }
    }
    &__freepikAttribute {
        position: absolute;
        left: 5px;
        bottom: 5px;
    }
}
