.blacksmith {
    display: flex;
    background-image: url(../../images/background_blacksmith.jpg);
    background-size: 100% 100%;
    position: relative;
    overflow: auto;
    height: 100%;
    &::after {
        content: "";
        display: block;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background: rgba(255, 255, 255, 0.3);
    }
    @include ipadPro {
        flex-direction: column-reverse;
    }
    @include tablet {
        flex-direction: column;
        align-items: center;
    }
    &::-webkit-scrollbar {
        width: 0;
    }
    &__item {
        height: 100%;
        width: 50%;
        padding: 10px;
        z-index: 2;
        position: relative;
        &::-webkit-scrollbar {
            width: 0;
        }
        &:first-child {
            border-right: 6px solid $color-gray_dark;
            @include laptopL {
                border-right: 4px solid $color-gray_dark;
            }
            @include tablet {
                border-right: none;
            }
        }
        &:last-child {
            @include ipadPro {
                overflow: hidden;
                flex-direction: row;
                height: 1665px;
                padding-top: 45px;
                border-bottom: 10px solid $color-gray_dark;
            }
            @include tablet {
                padding-top: 23px;
                overflow: auto;
                flex-direction: column;
                align-items: center;
                border-bottom: none;
                height: auto;
                justify-content: flex-start;
            }
            @include mobileL {
                height: 582px;
            }
            @include mobileS {
                height: 499px;
            }
        }
        &:first-child {
            @include ipadPro {
                display: flex;
                flex-direction: row;
            }
            @include tablet {
                flex-direction: column;
            }
        }
        @include laptopL {
            padding: 6px;
        }
        @include ipadPro {
            width: 1024px;
        }
        @include tablet {
            width: 768px;
        }
        @include mobileL {
            width: 425px;
        }
        @include mobileM {
            width: 375px;
        }
        @include mobileS {
            width: 320px;
        }
    }
    &__frame {
        background-image: url(../../images/blacksmith_item_frame.png);
    }
    .profile__equipment {
        @include ipadPro {
            width: 50%;
        }
        @include tablet {
            width: 100%;
        }
    }
    .profile__backpack {
        @include ipadPro {
            width: 50%;
            justify-content: flex-start;
        }
        @include tablet {
            width: 100%;
        }
    }
    &__mobileTable {
        display: none;
        width: 366px;
        height: auto;
        object-fit: cover;
        margin: 0 auto;
        margin-bottom: -54px;
        @include ipadPro {
            display: block;
        }
        @include tablet {
            display: none;
        }
    }
}
